<template>
<div id="gsdz">
  <div class="pc w1200">
	<h1></h1>
	<div class="box">
		<div class="lbox">
			<img src="@/assets/ppjjImg01.jpg" alt="">
			<h3></h3>
			<p>INNOCUBERINNOCUBER</p>
		</div>
		<div class="rbox">
			<div class="rboxTitle"><span></span><span style="color:#91919a"></span></div>
			<ppjjList />
			<div class="syhz">
				<b></b>
				<p>web</p>
				<router-link to=''>></router-link>
			</div>
		</div>
	</div>
  </div>
</div>
</template>

<script>
import ppjjList from '@/components/ppjjList'
export default {
	components:{ ppjjList },
	
}

$(function(){

	// 
  if(/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
      $(".mobile").show()
      $(".pc").hide()
  } else {
      $(".pc").show()
      $(".mobile").hide()
  }
})

</script>

<style scoped>
#gsdz{ margin-top: 78px }
.w1200{ width: 1200px; margin: 0 auto; }
h1{ text-align: center; font-weight: bold; font-size: 18px; margin: 20px auto; 
border-left:5px solid #333; border-right:5px solid #333; width:104px; height: 27px; line-height: 27px}
.box{ display: flex; justify-content: space-between }
.lbox{ width: 728px;  line-height: 1.8}
.lbox h3{ font-size: 16px;  border-bottom: 2px solid #333; width: 64px; margin: 50px 0 15px; }
.rbox{ width: 286px; }
.rbox .rboxTitle{ display: flex; justify-content: space-between;
 border-bottom: 2px solid #333; height: 40px; line-height: 40px;
width:284px;}
.rbox li{ height: 55px; line-height: 55px; border-bottom: 1px dotted #ddd;
background: url('~@/assets/lineIcon.jpg') 10px center no-repeat}
.rbox li:last-child{ border-bottom: none }
.rbox li a{ display: inline-block; color: #333; text-decoration: none;
 padding-left: 30px; width: 89%; font-weight: bold; font-size: 15px }
.rbox li img{ margin-left: 10px; vertical-align: middle }
.rbox li.active{ background: #f1f1f1 url('~@/assets/lineIcon.jpg') 10px center no-repeat }
.syhz{ margin: 40px 0 0 10px; line-height: 2; font-size: 14px}
.syhz b{ font-size: 16px }
.syhz a{ display: block; background: #999; width: 100%; height: 40px; margin: 15px 0 0;
	line-height: 40px; color:#fff; text-decoration: none; text-align: center}
</style>


